<template>
  <div class="withdrawal mescroll">
    <div class="mid">
      <p class="title">{{$route.query.type == '1'?'提现':'转出'}}金额</p>
      <div class="flex">
        <p class="font">¥</p>
        <input type="number" v-model="price" placeholder="请输入" />
      </div>
      <p class="text">
        账户余额：¥{{allPrice.toFixed(2)}}
        <span @click="all()">全部{{$route.query.type == '1'?'提现':'转出'}}</span>
      </p>
      <div class="btn" @click="submit">{{$route.query.type == '1'?'提 现':'转 出'}}</div>
      <div class="bottom">
        <p class="prompt">温馨提示</p>
        <p class="promptContent">
          {{$route.query.type == '1'?'小额提现秒到；大额提现，一个工作日到账。':'佣金转移至应付款后，应付款只能用来支付，不可转出。'}}
        </p>
        <p class="prompt" v-if="$route.query.type == '2'">另</p>
        <p class="promptContent"  v-if="$route.query.type == '2'" @click="toJump()">
              转出给好友，请点击.
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { Field, Dialog } from "vant";
import { getPersonAccount, withdrawal, toBalance } from "@/api";
import { element } from "prop-types";
export default {
  components: {
    [Field.name]: Field,
    [Dialog.name]: Dialog
  },
  data() {
    return {
      price: "",
      allPrice: 0,
      clickFlag: true,
      userCode: ""
    };
  },
  methods: {
    all() {
      this.price = this.allPrice;
    },
    toJump(){
      this.$router.push({
        name: "paytoFriends"
      });
    },
    submit() {
      if (!this.clickFlag) {
        return;
      }

      if (!this.price) {
        this.$toast("请输入提现金额");
        return;
      }
      if (this.$route.query.type == "1" && this.price < 1) {
        this.$toast("提现金额不得小于1");
        return;
      }
      if (this.$route.query.type == "1" && this.price > 5000) {
        this.$toast("提现金额不得大于5000");
        return;
      }
      if (this.$route.query.type == "2" && this.price <= 0) {
        this.$toast("转出金额必须大于0");
        return;
      }
      if (Number(this.price) > this.allPrice) {
        this.$toast("余额不足");
        return;
      }
      this.clickFlag = false;
      console.log(111);
      let that = this;
      // if (this.price > 200) {
      Dialog.confirm({
        title: "提示",
        message: 
          this.$route.query.type == "1"
            ? "小额提现秒到；大额提现，一个工作日到账。"
            : "佣金转移至余额后，余额只能用来支付，不可转出。"
      })
        .then(() => {
          // on confirm
          that.$toast.allowMultiple();
          const loading = that.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true // 禁用背景点击
          });
         if (that.$route.query.type == "1") {
            withdrawal({
              userCode: this.userCode,
              moneyNum: parseInt(this.price * 100)
            }).then(res => {
              loading.clear();
              if (res.code == 0) { 

                  that.$toast.success(res.msg);
                  setTimeout(() => {
                    that.$router.back();
                  }, 1000);
                 
              } else {
                that.clickFlag = true;
                that.$toast(res.msg);
              }
            });
          } else if (that.$route.query.type == "2") {
            toBalance({
              userCode: this.userCode,
              moneyNum: parseInt(this.price * 100)
            }).then(res => {
              loading.clear();
              if (res.code == 0) {
                that.$toast.success(res.msg);
                setTimeout(() => {
                  that.$router.back();
                }, 1000);
              } else {
                that.clickFlag = true;
                that.$toast(res.msg);
              }
            });
          } else {
            console.log(111);
          }
        })
        .catch(() => {
          // on cancel
          that.clickFlag = true;
        });
      // } else {
      //   this.$toast.allowMultiple();
      //   const loading = this.$toast.loading({
      //     duration: 0, // 持续展示 toast
      //     forbidClick: true // 禁用背景点击
      //   });
      //   withdrawal({
      //     userCode: this.userCode,
      //     moneyNum: parseInt(this.price * 100)
      //   }).then(res => {
      //     loading.clear();
      //     if (res.code == 0) {
      //       this.$toast.success("提现成功");
      //       setTimeout(() => {
      //         this.$router.back();
      //       }, 1000);
      //     } else {
      //       this.clickFlag = true;
      //       this.$toast(res.msg);
      //     }
      //   });
      // }
    },
    loadData() {
      this.$toast.allowMultiple();
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getPersonAccount().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.allPrice = res.data.myAccount.acAmt;
          this.userCode = res.data.myAccount.userCode;
        } else {
          this.$toast.fail(res.msg);
        }
      });
    }
  },
  mounted() {
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.withdrawal {
  background: #fff;
  height: 100%;
  .mid {
    padding: 0 15px;
    .title {
      padding: 10px 0;
      font-size: 15px;
      line-height: 25px;
      color: #999;
    }
    .flex {
      display: flex;
      border-bottom: 1pt solid #e1e1e1;
      padding: 5px 0 10px;
      .font {
        margin-right: 5px;
      }
      input {
        border: 0;
        width: 100%;
        height: 30px;
        font-size: 30px;
        line-height: 30px;
      }
    }
    .text {
      color: #666;
      line-height: 25px;
      font-size: 15px;
      margin: 10px 0;
      span {
        color: #eb5252;
        margin-left: 5px;
      }
    }
    .btn {
      background: #eb5252;
      color: #fff;
      margin: 20px 0 0;
      height: 50px;
      text-align: center;
      line-height: 50px;
      letter-spacing: 1px;
      border-radius: 5px;
    }
    .bottom {
      position: fixed;
      bottom: 50px;
      width: 100%;
      left: 0;
      text-align: center;
      line-height: 22px;
      color: #999;
      .prompt {
        font-weight: bold;
        font-size: 15px;
      }
      .promptContent {
        font-size: 13px;
      }
    }
  }
}
</style>
